import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import logo from './logo.svg';
import './App.css';
import { firebase } from './Config';

const todoRefInfo = firebase.firestore().collection('pembuat');
const todoRefOwn = firebase.firestore().collection('pengguna');

function createData(judul, nowa, email, namatoko) {
  return { judul, nowa,email, namatoko };
}

let rows = [
  createData('', '', '', )
];

export default function App() {
  const [data, setData] = React.useState([createData('', '', '', )])

  const getData = async() => {

    todoRefOwn
    .onSnapshot(    
        querySnapshot => {

            let user = [];


            firebase.firestore().collection('produk')
            .onSnapshot(    
                querySnapshot => {

                    rows = []
                
                    querySnapshot.forEach((doc) => {
                        
                   
                        const { judul, nowa, namatoko, deskripsi, stok, harga, gambarc, gambarb, gambara, email, ig, lat, long } = doc.data();
                        
                      //   prod.push({
                      //     id: doc.id,
                      //     judul,
                      //     deskripsi,
                      //     gambara,
                      //     gambarb,
                      //     gambarc,
                      //     stok,
                      //     harga,
                      //     email,
                      //     nowa, 
                      //     ig, 
                      //     namatoko, 
                      //     lat, 
                      //     long,
                      //     jarak:[lat,long],
                          
                      // })
                    
                      rows.push(createData(
                        judul,
                        nowa, 
                        email,
                        namatoko, 
                        doc.id,
                        
                        deskripsi,
                        gambara,
                        gambarb,
                        gambarc,
                        stok,
                        harga,
                        
                        ig, 
                        
                        lat, 
                        long,
                        [lat,long]
                        )  
                      )

                  
                    })
                    setData(rows)
                    // console.log(rows)


                }
            )
        
            
        }
    )
}

  React.useEffect(() => {

    getData();
    
  }, [])

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table" >
        <TableHead style={{backgroundColor:'#97BE5A', fontWeight:'bold'}}>
          <TableRow>
            <TableCell>Nama Produk</TableCell>
            <TableCell>No.WA</TableCell>
            <TableCell>Email</TableCell>
            <TableCell>Penjual</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row) => (
            <TableRow
              key={row.judul}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              {console.log(row)}
              <TableCell component="th" scope="row">{row.judul}</TableCell>
              <TableCell><a target='_blank' href={'https://wa.me/'+row.nowa.replace(/[0-9]{1,2}/, "+628")}>{row.nowa}</a></TableCell>
              <TableCell>{row.email}</TableCell>
              <TableCell>{row.namatoko}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

